.header {
  height: 80px;
  background: #4045BA;
  display: flex;
  align-items: center;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo a {
  font-size: 24px;
  line-height: 29px;
  font-weight: 600;
}

.header__nav ul {
  display: flex;
  font-weight: 600;
}

.header__nav ul li {
  padding: 8px 16px;
  border-radius: 10px;
  cursor: pointer;
}

.header__nav ul li a {
  color: #000000;
}

.header__nav ul li:first-child {
  background-color: #ffffff;
}

.header__nav ul li:first-child a {
  color: #4045BA;
}

.header__nav ul li:last-child {
  border: 1px solid #000000;
  background-color: #A549D0;
}

.header__nav ul li:nth-child(2),
.header__nav ul li:nth-child(3),
.header__nav ul li:nth-child(4),
.header__nav ul li:nth-child(5) {
  margin-left: 35px;
}

.header__nav ul li:nth-child(2) a,
.header__nav ul li:nth-child(3) a,
.header__nav ul li:nth-child(4) a,
.header__nav ul li:nth-child(5) a {
  color: #ffffff;
}

.header__nav ul li:nth-child(2):hover,
.header__nav ul li:nth-child(3):hover,
.header__nav ul li:nth-child(4):hover {
  background-color: #ffffff;
}

.header__nav ul li:nth-child(2):hover a,
.header__nav ul li:nth-child(3):hover a,
.header__nav ul li:nth-child(4):hover a {
  color: #4045BA;
}

html * {
  box-sizing: border-box;
}

body {
  font-family: "Gilroy", sans-serif;
  font-size: 14px;
}

.container {
  max-width: 1224px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
}

h2 {
  margin: 0;
  padding: 0;
}

p {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: #ffffff;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

.top {
  border-top: 1px solid red;
  background-color: #4045BA;
  padding-top: 55px;
  padding-bottom: 115px;
}

.top h3 {
  font-size: 24px;
  line-height: 29px;
  color: #ffffff;
  margin-bottom: 25px;
}

.top__wrapper {
  display: flex;
}

.top__left {
  max-width: 288px;
  width: 100%;
}

.top__left input {
  width: 100%;
  padding-left: 12px;
  padding-right: 12px;
  margin-bottom: 15px;
  line-height: 46px;
  background-color: rgba(255, 255, 255, 0.1);
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  font-weight: 300;
  color: #ffffff;
}

.top__left input::placeholder {
  color: #ffffff;
}

.top__left p {
  color: #ffffff;
  font-size: 14px;
  line-height: 16px;
  width: 100%;
}

.top__left .btn-box {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.top__left .button-left {
  position: relative;
  width: 136px;
}

.top__left .button-left:before {
  position: absolute;
  content: url("../img/mcafee.png");
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.top__left .button-right {
  position: relative;
  width: 136px;
}

.top__left .button-right:before {
  position: absolute;
  content: url("../img/norton-security.png");
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.top__left .button-join {
  width: 100%;
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
  color: #ffffff;
  background-color: #A549D0;
  margin-top: 15px;
}

.top__left strong {
  display: block;
  text-align: center;
  color: #ffffff;
  line-height: 17px;
  font-weight: 600;
  margin: 12px 0;
}

.top__left .button-facebook {
  font-size: 16px;
  line-height: 19px;
  color: #2D4486;
  font-weight: 600;
  position: relative;
}

.top__left .button-facebook:before {
  position: absolute;
  font-family: "icomoon";
  content: "\e905";
  left: 0;
  top: 0;
  font-size: 24px;
  color: red;
}

.top__right {
  max-width: 900px;
  width: 100%;
  height: 411px;
  border: 8px solid #ffffff;
  border-radius: 20px;
  margin-left: 25px;
  background-image: url("../img/top-image.png");
  background-position: center;
}

.top__right h3 {
  margin-top: 45px;
  background-color: rgba(255, 255, 255, 0.3);
  padding: 24px 32px;
  color: #352f55;
  font-size: 24px;
  line-height: 28px;
  font-weight: 600;
}

.top__right-item ul {
  background-color: rgba(255, 255, 255, 0.3);
  padding: 16px 32px;
}

.top__right-item ul li {
  font-size: 18px;
  line-height: 29px;
  color: #352f55;
  font-weight: 600;
}

.top__right-item ul li:nth-child(3),
.top__right-item ul li:nth-child(4) {
  text-transform: uppercase;
}

.top__right-item ul li:nth-child(4) span {
  text-transform: capitalize;
}

button {
  width: 288px;
  height: 40px;
  border-radius: 10px;
  background: #ffffff;
  outline: none;
  border: none;
}

.search__wrapper {
  margin: 0px -12px;
  display: flex;
  flex-wrap: wrap;
}

.search__item {
  width: 33.3%;
  padding: 0 12px;
  margin-bottom: 24px;
}

.search__item-inner {
  background-color: #E5E5E5;
  border-radius: 10px;
  padding: 8px;
  display: flex;
}

.search__item img {
  width: 192px;
}

.search__item-info {
  padding-left: 24px;
  padding-top: 40px;
}

.search__item-name {
  font-size: 18px;
  line-height: 21px;
  font-weight: 600;
  color: #352f55;
  margin-bottom: 13px;
}

.search__item-age {
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  margin-top: 13px;
}

.search__item-age span {
  font-weight: 600;
  line-height: 17px;
}

.search__item-block {
  margin-top: 15px;
}

.search__item-block li {
  margin-bottom: 12px;
}

.search__item-block li a {
  display: flex;
  align-items: center;
}

.search__item-block li a i {
  width: 32px;
  height: 32px;
  border-radius: 5px;
}

.search__item-block li a i.chat-icon {
  background-color: rgba(64, 69, 186, 0.2);
  position: relative;
}

.search__item-block li a i.chat-icon:before {
  position: absolute;
  content: "\e901";
  font-family: 'icomoon';
  left: 0;
  top: 0;
}

.search__item-block li a i.message-icon {
  background-color: rgba(206, 73, 208, 0.2);
}

.search__item-block li a i.clips-icon {
  background-color: rgba(237, 110, 127, 0.2);
}

.search__item-block li a span {
  font-size: 12px;
  line-height: 14px;
  padding-left: 12px;
  color: #352f55;
}

.search__item-block li:last-child {
  margin-bottom: 0;
}

.search__item-id {
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  margin-top: 22px;
}
